* {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
}

.slick-list {
  width: 100%;
  height: 300px;
  font-size: 30px;
  margin-top: 35px;
  text-align: center;
  color: #2D918E;
}

.slick-dots {
  bottom: -25px;
  width: 100%;
  margin: 0;
  list-style: none;
  text-align: center;
  font-size: 30px;
}

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: red;
  border: 0;
  outline: 0;
  background: 0 0;
}

.slick-slide img {
  display: inline;
  margin-top: 15px;
}

.test-descrp {
  color: black;
  font-size: 20px;
}



