}

.navBar {
  position: fixed;
  z-index: 999;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: start;
      align-items: flex-start;
}

.textNavBar {
  padding: 10px 0;
  width: 40%;
  position: absolute;
  top: 0;
  right: 0;
  background: #302626;
  color: #E3F4F4;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  font-size: 20px;
  text-align: center;
}

.logoDiv {
  position: relative;
  top: 1%;
  left: 1%
}



